.map {
  position: relative;
  width: 100%;
  max-width: 100%;
  margin-top: 0;

  // background-image: url('../img/clubs/map.jpg');
  background-size: cover;

  height: 610px;
  overflow: hidden;

  &__ya {
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    height: 610px;
    overflow: hidden;

    -ms-touch-action: auto !important;
    touch-action: auto !important;
  }

  & .container {
    position: relative;
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    height: 100%;

    @media (min-width: $md) {
      max-width: 1440px;
    }
  }

  &__info {
    z-index: 3;
    position: absolute;
    bottom: 0;
    padding: 10% 5%;
    background-color: $white-color;
    width: 100%;
    max-width: 370px;
    border-radius: 10px;

    @media (min-width: $sm) {
      bottom: auto;
      top: 10%;
      left: 0;
    }
    @media (min-width: $md) {
      padding: 5%;
      max-width: 610px;
      border-radius: 0;
    }

    &-wrapper {
      max-width: 282px;
      margin: 0 auto;
    }

    & h3 {
      margin-bottom: 38px;
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      color: $primary-color;
      text-transform: uppercase;
    }

    & ul {
      font-weight: 400;
      font-size: 18px;
      line-height: 130%;
      color: $dark-color;
    }

    & li {
      margin-bottom: 15px;
    }
  }
}
