.modal {
  font-family: $mainFont;

  &-lg {
    max-width: 733px;
  }

  & .title {
    margin-bottom: 48px;
    font-family: $mainFont;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 38px;

    /* identical to box height, or 122% */

    text-align: center;
    letter-spacing: -0.02em;

    /* Gray/900 */

    color: #101828;

    @media (min-width: $sm) {
      margin-bottom: 54px;
      font-size: 36px;
      line-height: 44px;
    }
  }
}
