.navigation {
  display: none;
  z-index: 33;
  position: fixed;
  top: 0;
  right: 0;
  width: 495px;
  height: 402px;
  max-width: 100%;
  padding: 54px;
  background-color: $blue-color;

  transform: translateX(100%);
  transition: 0.3s;
  opacity: 0;

  &.open {
    display: block;
    transform: translateX(0);
    opacity: 1;
  }

  &__close {
    position: absolute;
    top: 28px;
    right: 28px;
    @include buttonReset();
  }

  &__list {
    padding-bottom: 16px;
    margin-bottom: 40px;
    border-bottom: 1px solid $white-color;
  }

  &__item {
    margin-bottom: 24px;
    & a {
      display: block;
      text-transform: uppercase;
    }
  }
}

.overlay {
  z-index: 11;
  background-color: rgba(23,24,24,.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  overflow: hidden;
}
