.page-header {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 65px;
  background-color: $blue-color;
  padding-top: 20px;
  padding-bottom: 20px;

  & .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  &__label {
    display: none;
    font-weight: 400;
    font-size: 13px;
    line-height: 100%;
    text-transform: uppercase;
    color: $white-color;

    @media (min-width: $xs) {
      display: flex;
      font-size: 15px;
    }
  }

  &__btn-menu {
    @include buttonReset();
  }
}
