.timer {
  padding-top: 34px;
  padding-bottom: 100px;
  background-color: $white-color;

  @media (min-width: $md) {
    padding-bottom: 90px;
    padding-top: 50px;
  }


  & .title {
    margin-bottom: 24px;
    font-weight: 700;
    font-size: 19px;
    color: $dark-color;
    text-align: center;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        375px: 19px,
        768px: 22px,
        1200px: 30px,
      )
    );

    @media (min-width: $sm) {
      margin-bottom: 34px;
      font-size: 22px;
      line-height: 140%;
    }
    @media (min-width: $md) {
      font-size: 30px;

    }
  }

  &__wrapper {
    margin-bottom: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    width: 100%;

    // @media (min-width: $sm) {
    //   justify-content: flex-end;
    //   width: 65%;
    // }
    // @media (min-width: $md) {
    //   justify-content: space-between;
    //   width: auto;
    // }
    // @media (min-width: $xl) {
    //   width: 56%;
    // }
    @media (min-width: $xxl) {
      max-width: 600px;
      // width: 60%;
      margin: 0 auto;
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 22vw;
    height: 22vw;

    @media (min-width: 375px) {
      width: 90px;
      height: 90px;
    }
    @media (min-width: $sm) {
      width: 182px;
      height: 182px;
    }


    & svg {
      position: relative;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(270deg);
      width: 100%;
      height: 100%;
      fill: transparent;
      stroke-width: 10;
      stroke: #f3f6f8;

      & .circle {
        // stroke: var(--clr);
        // stroke: $blue-color;
        stroke-dasharray: 440;
        stroke-linecap: round;
      }
    }

    & .dots {
      z-index: 100;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: flex-start;

      // &::before {
      //   content: '';
      //   position: absolute;
      //   top: 0;
      //   left: 0;
      //   width: 15px;
      //   height: 15px;
      //   background-color: $blue-color;
      //   box-shadow: $boxShadow;
      // }
    }
  }

  &__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    line-height: normal;
    letter-spacing: 0.02em;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 21px,
        768px: 38px,
        1280px: 45px,
        // 1280px: 52px,
      )
    );

    & span {
      @include ff(
        'font-size',
        (
          320px: 9px,
          500px: 14px,
          769px: 10px,
          1024px: 14px,
          1440px: 17px,
          // 1440px: 24px,
        )
      );
    }
  }
}
