.accordion {
  position: relative;
  cursor: pointer;
  padding: 22px;
  padding-right: 50px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  transition: 0.4s;
  // border-top: 1px solid $border-color;
  border-bottom: 1px solid $border-color;
  // margin-top: -1px;
  background-color: transparent;
  color:$dark-color;

  &::before {
    content: '';
    position: absolute;
    right: 22px;
    top: 50%;
    width: 25px;
    height: 25px;
    transform: translateY(-50%);
    background-repeat: no-repeat;
    background-image: url('../images/icons/plus.svg');
  }

  &:focus,
  &:active,
  &:hover {
    outline: none;
    background-color: transparent;

    &::before {
      transition: 0.4s;
      transform: translateY(-50%) rotate(0);
    }
  }

  &.active {
    &::before {
      transition: 0.3s;
      transform: translateY(-50%) rotate(45deg);
    }
  }
}

.active,
.accordion:hover {
  outline: none;
  background-color: transparent;
}

.panel {
  padding: 20px;

  display: none;
  overflow: hidden;
  animation: 0.5s acctext forwards;
}

@keyframes acctext {
  from {
    opacity: 0;
    transform: translateY(0%);
  }

  to {
    opacity: 1;
    transform: translateY(5%);
  }
}
