.accordion {
  position: relative;
  cursor: pointer;
  padding: 22px;
  padding-right: 50px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  transition: 0.4s;
  border-bottom: 1px solid #171818;
  background-color: transparent;
  color: #171818;
}
.accordion::before {
  content: "";
  position: absolute;
  right: 22px;
  top: 50%;
  width: 25px;
  height: 25px;
  transform: translateY(-50%);
  background-repeat: no-repeat;
  background-image: url("../images/icons/plus.svg");
}
.accordion:focus, .accordion:active, .accordion:hover {
  outline: none;
  background-color: transparent;
}
.accordion:focus::before, .accordion:active::before, .accordion:hover::before {
  transition: 0.4s;
  transform: translateY(-50%) rotate(0);
}
.accordion.active::before {
  transition: 0.3s;
  transform: translateY(-50%) rotate(45deg);
}

.active,
.accordion:hover {
  outline: none;
  background-color: transparent;
}

