.profit {
  padding-top: 75px;
  padding-bottom: 54px;

  background-color: $blue-color;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-image: url('../images/profit/bg-1.webp');

  @media (min-width: $xs) {
    padding-top: 54px;
  }
  @media (min-width: $sm) {
    background-image: url('../images/profit/bg-1t.webp');
  }
  @media (min-width: $md) {
    background-image: url('../images/profit/bg-1d.webp');
  }

  & .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__title {
    margin-bottom: 50px;

    @media (min-width: $sm) {
      margin-bottom: 70px;
    }

    @media (min-width: $md) {
      margin-bottom: 60px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;


    @media (min-width: $xs) {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }

  &__item {
    margin-bottom: 60px;
    text-align: center;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    &:last-child {
      margin-bottom: 0;
    }


    @media (min-width: $xs) {
      width: calc(100%/2 - 50px);
      margin-bottom: 44px;

      &:first-child {
        width: 100%;
      }
    }
    @media (min-width: $md) {
      width: calc(100%/3 - 50px);

      &:first-child,
      &:nth-child(2) {
        width: calc(100%/2 - 50px);
      }

      &:first-child {
        & .profit__item-inner {
          margin-left: auto;

          &::before {
            height: 80px;
          }
        }
      }
      &:nth-child(2) {
        & .profit__item-inner {
          margin-right: auto;
        }
      }
    }
  }

  &__item-inner {
    position: relative;
    width: 100%;
    max-width: 247px;
    padding-top: calc(80px + 30px);

    @media (min-width: $sm) {
      max-width: 375px;
    }
    @media (min-width: $md) {
      max-width: 400px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 50%;
      height: 90px;
      width: 90px;
      transform: translateX(50%);
      background-image: var(--pic);
      background-repeat: no-repeat;
      background-position: 100% 0;
      background-size: contain;
    }
  }

  &__item-title {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: $yellow-color;
    text-transform: uppercase;

    @media (min-width: $sm) {
      font-size: 20px;
    }
  }

  &__item-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: $white-color;

    @media (min-width: $sm) {
      font-size: 20px;
    }
  }
}
