:root {
  --bg-w: 70px;

  @media (min-width: $xs) {
    --bg-w: 140px;
  }
  @media (min-width: $md) {
    --bg-w: 235px;
  }
  @media (min-width: 1600px) {
    --bg-w: 360px;
  }
}

.promo {
  margin-top: 65px;
  padding-top: calc(var(--bg-w) + 24px);
  padding-bottom: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: $dark-color;

  background: url('../images/promo/Mask group.png') var(--bg-w) no-repeat,
    url('../images/promo/Mask group-1.png') var(--bg-w) no-repeat,
    url('../images/promo/Mask group-2.png') var(--bg-w) no-repeat;

  background-position: 0 0, 52% 0, 100% 0;
  background-size: calc(100% / 3) var(--bg-w), calc(100% / 2.4) var(--bg-w),
    calc(100% / 3.2) var(--bg-w);

    @media (min-width: $xs) {
      padding-top: calc(var(--bg-w) + 54px);
    }
    @media (min-width: $md) {
      padding-top: calc(var(--bg-w) + 60px);
    }

  &__title {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 33px;
    line-height: 110%;
    text-transform: uppercase;

    @media (min-width: $xs) {
      font-size: 67px;
    }
    @media (min-width: $md) {
      font-size: 88px;
    }
  }

  &__price {
    padding: 0 15px;
    margin-bottom: 38px;
    font-weight: 400;
    font-size: 22px;
    line-height: 100%;
    text-transform: uppercase;

    @media (min-width: $xs) {
      font-size: 36px;
      padding: 0;
    }
    @media (min-width: $md) {
      font-size: 52px;
    }
  }

  &__text {
    margin-bottom: 34px;
    font-weight: 700;
    font-size: 19px;
    line-height: 140%;
    text-transform: uppercase;

    @media (min-width: $xs) {
      font-size: 22px;
    }
    @media (min-width: $md) {
      font-size: 24px;
    }
  }
}
